import React, {PropTypes} from 'react';

class Park extends React.Component {

  static propTypes = {
    name: PropTypes.string.isRequired
  };

  render() {
    return (
      <section>
        <h4>{this.props.name}</h4>
      </section>
    );
  }

}

export default Park;



